/**
 * TimelineVisualization - Interactive timeline for case events
 *
 * Features:
 * - Chronological event display
 * - Event categories (claims, contradictions, evidence)
 * - Zoom and pan controls
 * - Event details on hover/click
 */

import React, { useMemo, useState, useCallback } from 'react';
import {
  Calendar,
  AlertTriangle,
  FileText,
  MessageSquare,
  ChevronLeft,
  ChevronRight,
  ZoomIn,
  ZoomOut,
} from 'lucide-react';

// ===========================
// Types
// ===========================

export type TimelineEventType = 'claim' | 'contradiction' | 'evidence' | 'document' | 'statement';

export interface TimelineEvent {
  id: string;
  date: string; // ISO date string
  title: string;
  description?: string;
  type: TimelineEventType;
  severity?: 'critical' | 'high' | 'medium' | 'low';
  sourceId?: string;
  page?: number;
}

interface TimelineVisualizationProps {
  events: TimelineEvent[];
  onEventClick?: (event: TimelineEvent) => void;
  className?: string;
}

// ===========================
// Constants
// ===========================

const EVENT_TYPE_CONFIG: Record<
  TimelineEventType,
  { icon: React.ElementType; color: string; bgColor: string; label: string }
> = {
  claim: {
    icon: MessageSquare,
    color: 'text-blue-400',
    bgColor: 'bg-blue-500/20 border-blue-500/30',
    label: 'טענה',
  },
  contradiction: {
    icon: AlertTriangle,
    color: 'text-amber-400',
    bgColor: 'bg-amber-500/20 border-amber-500/30',
    label: 'סתירה',
  },
  evidence: {
    icon: FileText,
    color: 'text-green-400',
    bgColor: 'bg-green-500/20 border-green-500/30',
    label: 'ראיה',
  },
  document: {
    icon: FileText,
    color: 'text-purple-400',
    bgColor: 'bg-purple-500/20 border-purple-500/30',
    label: 'מסמך',
  },
  statement: {
    icon: MessageSquare,
    color: 'text-cyan-400',
    bgColor: 'bg-cyan-500/20 border-cyan-500/30',
    label: 'עדות',
  },
};

const SEVERITY_COLORS: Record<string, string> = {
  critical: 'border-red-500',
  high: 'border-orange-500',
  medium: 'border-yellow-500',
  low: 'border-gray-500',
};

// ===========================
// Helper Functions
// ===========================

function formatDate(dateStr: string): string {
  try {
    const date = new Date(dateStr);
    return date.toLocaleDateString('he-IL', {
      day: 'numeric',
      month: 'short',
      year: 'numeric',
    });
  } catch {
    return dateStr;
  }
}

function groupEventsByMonth(
  events: TimelineEvent[]
): Map<string, TimelineEvent[]> {
  const groups = new Map<string, TimelineEvent[]>();

  events.forEach((event) => {
    try {
      const date = new Date(event.date);
      const key = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}`;
      const existing = groups.get(key) || [];
      groups.set(key, [...existing, event]);
    } catch {
      // Skip invalid dates
    }
  });

  // Sort groups by date
  return new Map([...groups.entries()].sort((a, b) => a[0].localeCompare(b[0])));
}

function getMonthLabel(key: string): string {
  try {
    const [year, month] = key.split('-');
    const date = new Date(parseInt(year), parseInt(month) - 1);
    return date.toLocaleDateString('he-IL', { month: 'long', year: 'numeric' });
  } catch {
    return key;
  }
}

// ===========================
// Event Card Component
// ===========================

const EventCard: React.FC<{
  event: TimelineEvent;
  onClick?: () => void;
}> = ({ event, onClick }) => {
  const config = EVENT_TYPE_CONFIG[event.type];
  const Icon = config.icon;
  const severityBorder = event.severity ? SEVERITY_COLORS[event.severity] : '';

  return (
    <button
      onClick={onClick}
      className={`
        w-full text-right p-3 rounded-lg border transition-all
        hover:scale-[1.02] hover:shadow-lg
        ${config.bgColor}
        ${severityBorder ? `border-r-4 ${severityBorder}` : ''}
      `}
    >
      <div className="flex items-start gap-3">
        <div className={`p-2 rounded-lg bg-slate-800/50 ${config.color}`}>
          <Icon className="w-4 h-4" />
        </div>

        <div className="flex-1 min-w-0">
          <div className="flex items-center gap-2 mb-1">
            <span className={`text-xs font-medium ${config.color}`}>
              {config.label}
            </span>
            <span className="text-[10px] text-white/40">
              {formatDate(event.date)}
            </span>
          </div>

          <h4 className="text-sm font-medium text-white truncate">
            {event.title}
          </h4>

          {event.description && (
            <p className="text-xs text-white/60 mt-1 line-clamp-2">
              {event.description}
            </p>
          )}
        </div>
      </div>
    </button>
  );
};

// ===========================
// Month Group Component
// ===========================

const MonthGroup: React.FC<{
  monthKey: string;
  events: TimelineEvent[];
  onEventClick?: (event: TimelineEvent) => void;
}> = ({ monthKey, events, onEventClick }) => {
  // Sort events by date
  const sortedEvents = useMemo(
    () =>
      [...events].sort(
        (a, b) => new Date(a.date).getTime() - new Date(b.date).getTime()
      ),
    [events]
  );

  return (
    <div className="relative">
      {/* Month header */}
      <div className="sticky top-0 z-10 flex items-center gap-3 mb-4 bg-slate-900/95 py-2">
        <div className="w-3 h-3 rounded-full bg-blue-500 border-2 border-slate-900" />
        <h3 className="text-sm font-semibold text-white">
          {getMonthLabel(monthKey)}
        </h3>
        <span className="text-xs text-white/40">({events.length} אירועים)</span>
      </div>

      {/* Events */}
      <div className="mr-6 pr-4 border-r-2 border-slate-700 space-y-3">
        {sortedEvents.map((event) => (
          <EventCard
            key={event.id}
            event={event}
            onClick={() => onEventClick?.(event)}
          />
        ))}
      </div>
    </div>
  );
};

// ===========================
// Filter Chips
// ===========================

const FilterChips: React.FC<{
  activeFilters: Set<TimelineEventType>;
  onToggle: (type: TimelineEventType) => void;
  eventCounts: Record<TimelineEventType, number>;
}> = ({ activeFilters, onToggle, eventCounts }) => {
  return (
    <div className="flex flex-wrap gap-2">
      {(Object.keys(EVENT_TYPE_CONFIG) as TimelineEventType[]).map((type) => {
        const config = EVENT_TYPE_CONFIG[type];
        const count = eventCounts[type] || 0;
        const isActive = activeFilters.has(type);

        return (
          <button
            key={type}
            onClick={() => onToggle(type)}
            className={`
              flex items-center gap-1.5 px-2.5 py-1.5 rounded-lg text-xs font-medium
              border transition-all
              ${
                isActive
                  ? `${config.bgColor} ${config.color}`
                  : 'bg-slate-800/50 text-white/40 border-slate-700 hover:text-white/60'
              }
            `}
          >
            <span>{config.label}</span>
            <span
              className={`px-1.5 py-0.5 rounded text-[10px] ${
                isActive ? 'bg-white/10' : 'bg-slate-700'
              }`}
            >
              {count}
            </span>
          </button>
        );
      })}
    </div>
  );
};

// ===========================
// Main Component
// ===========================

export const TimelineVisualization: React.FC<TimelineVisualizationProps> = ({
  events,
  onEventClick,
  className = '',
}) => {
  // Filter state
  const [activeFilters, setActiveFilters] = useState<Set<TimelineEventType>>(
    new Set(Object.keys(EVENT_TYPE_CONFIG) as TimelineEventType[])
  );

  // Toggle filter
  const toggleFilter = useCallback((type: TimelineEventType) => {
    setActiveFilters((prev) => {
      const next = new Set(prev);
      if (next.has(type)) {
        next.delete(type);
      } else {
        next.add(type);
      }
      return next;
    });
  }, []);

  // Count events by type
  const eventCounts = useMemo(() => {
    const counts: Record<TimelineEventType, number> = {
      claim: 0,
      contradiction: 0,
      evidence: 0,
      document: 0,
      statement: 0,
    };
    events.forEach((e) => {
      counts[e.type] = (counts[e.type] || 0) + 1;
    });
    return counts;
  }, [events]);

  // Filter events
  const filteredEvents = useMemo(
    () => events.filter((e) => activeFilters.has(e.type)),
    [events, activeFilters]
  );

  // Group events by month
  const groupedEvents = useMemo(
    () => groupEventsByMonth(filteredEvents),
    [filteredEvents]
  );

  if (events.length === 0) {
    return (
      <div className={`flex items-center justify-center h-64 ${className}`}>
        <div className="text-center">
          <Calendar className="w-12 h-12 text-white/20 mx-auto mb-3" />
          <p className="text-white/40 text-sm">אין אירועים לתצוגה</p>
        </div>
      </div>
    );
  }

  return (
    <div className={`flex flex-col h-full ${className}`} dir="rtl">
      {/* Header with filters */}
      <div className="px-4 py-3 border-b border-slate-700 bg-slate-800/50">
        <div className="flex items-center justify-between mb-3">
          <h2 className="text-lg font-semibold text-white flex items-center gap-2">
            <Calendar className="w-5 h-5 text-blue-400" />
            ציר זמן
          </h2>
          <span className="text-xs text-white/40">
            {filteredEvents.length} מתוך {events.length} אירועים
          </span>
        </div>

        <FilterChips
          activeFilters={activeFilters}
          onToggle={toggleFilter}
          eventCounts={eventCounts}
        />
      </div>

      {/* Timeline content */}
      <div className="flex-1 overflow-y-auto p-4 space-y-8">
        {Array.from(groupedEvents.entries()).map(([monthKey, monthEvents]) => (
          <MonthGroup
            key={monthKey}
            monthKey={monthKey}
            events={monthEvents}
            onEventClick={onEventClick}
          />
        ))}
      </div>
    </div>
  );
};

export default TimelineVisualization;
