/**
 * CaseAnalyticsPage - Analytics dashboard for a specific case
 *
 * Shows:
 * - Timeline visualization
 * - Document heatmaps
 * - Distribution charts
 * - Claim matrix
 */

import React, { useMemo } from 'react';
import { useParams, useNavigate, Link } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { ArrowRight, BarChart2, Loader2 } from 'lucide-react';
import { apiGet } from '../utils/api';
import {
  TimelineVisualization,
  DocumentHeatmap,
  DistributionChart,
  ClaimMatrix,
} from '../components/analytics';
import type { TimelineEvent, DocumentHeatmapData, ChartDataItem, ClaimMatrixData } from '../components/analytics';

export function CaseAnalyticsPage() {
  const { caseId } = useParams<{ caseId: string }>();
  const navigate = useNavigate();

  // Fetch case data
  const { data: caseData, isLoading } = useQuery({
    queryKey: ['case', caseId],
    queryFn: () => apiGet(`/api/cases/${caseId}`),
    enabled: !!caseId,
  });

  // Convert case data to analytics formats
  const timelineEvents = useMemo<TimelineEvent[]>(() => {
    if (!caseData) return [];
    const events: TimelineEvent[] = [];

    // Add claims as events
    (caseData.claims || []).forEach((claim: any, idx: number) => {
      if (claim.date || claim.created_at) {
        events.push({
          id: `claim-${idx}`,
          date: claim.date || claim.created_at,
          title: claim.text?.substring(0, 100) || 'טענה',
          type: 'claim',
          sourceId: claim.source_id,
        });
      }
    });

    // Add contradictions as events
    (caseData.contradictions || []).forEach((c: any, idx: number) => {
      events.push({
        id: `contradiction-${idx}`,
        date: c.detected_at || new Date().toISOString(),
        title: c.description || 'סתירה',
        type: 'contradiction',
        severity: c.severity,
      });
    });

    // Add timeline events
    (caseData.timeline || []).forEach((event: any, idx: number) => {
      events.push({
        id: `timeline-${idx}`,
        date: event.date || event.timestamp,
        title: event.description || event.text,
        type: 'evidence',
      });
    });

    return events;
  }, [caseData]);

  // Convert to heatmap data
  const heatmapData = useMemo<DocumentHeatmapData[]>(() => {
    if (!caseData?.files_data) return [];

    return (caseData.files_data || []).map((file: any) => {
      const pageData = [];
      const numPages = file.pages || file.page_count || 10;

      // Create page data based on claims/evidence locations
      for (let page = 1; page <= numPages; page++) {
        const evidenceCount = (caseData.claims || []).filter(
          (c: any) => c.source_id === file.id && c.page === page
        ).length;
        const contradictionCount = (caseData.contradictions || []).filter(
          (c: any) =>
            (c.source_a === file.id && c.page_a === page) ||
            (c.source_b === file.id && c.page_b === page)
        ).length;

        pageData.push({
          page,
          evidenceCount,
          contradictionCount,
          claimCount: evidenceCount,
          totalScore: evidenceCount + contradictionCount * 2,
        });
      }

      return {
        documentId: file.id,
        documentName: file.name || file.filename,
        totalPages: numPages,
        pageData,
      };
    });
  }, [caseData]);

  // Distribution data
  const claimsByType = useMemo<ChartDataItem[]>(() => {
    if (!caseData?.claims) return [];
    const counts: Record<string, number> = {};
    (caseData.claims || []).forEach((claim: any) => {
      const type = claim.type || claim.category || 'כללי';
      counts[type] = (counts[type] || 0) + 1;
    });
    return Object.entries(counts).map(([label, value]) => ({ label, value }));
  }, [caseData]);

  const contradictionsBySeverity = useMemo<ChartDataItem[]>(() => {
    if (!caseData?.contradictions) return [];
    const counts: Record<string, number> = { critical: 0, high: 0, medium: 0, low: 0 };
    (caseData.contradictions || []).forEach((c: any) => {
      const sev = c.severity || 'medium';
      counts[sev] = (counts[sev] || 0) + 1;
    });
    return [
      { label: 'קריטי', value: counts.critical, color: 'bg-red-500' },
      { label: 'גבוה', value: counts.high, color: 'bg-orange-500' },
      { label: 'בינוני', value: counts.medium, color: 'bg-yellow-500' },
      { label: 'נמוך', value: counts.low, color: 'bg-gray-500' },
    ].filter((d) => d.value > 0);
  }, [caseData]);

  // Claim matrix data
  const matrixData = useMemo<ClaimMatrixData>(() => {
    if (!caseData) return { claims: [], entities: [], relationships: [] };

    const claims = (caseData.claims || []).slice(0, 20).map((c: any, idx: number) => ({
      id: `claim-${idx}`,
      label: c.text?.substring(0, 30) || `טענה ${idx + 1}`,
    }));

    const entities = (caseData.entities || []).slice(0, 15).map((e: any, idx: number) => ({
      id: `entity-${idx}`,
      label: e.name || `ישות ${idx + 1}`,
    }));

    // Generate relationships based on mentions
    const relationships: ClaimMatrixData['relationships'] = [];
    claims.forEach((claim: any, ci: number) => {
      entities.forEach((entity: any, ei: number) => {
        // Simulate relationship based on random but deterministic pattern
        if ((ci + ei) % 3 === 0) {
          relationships.push({
            claimId: claim.id,
            entityId: entity.id,
            strength: (ci + ei) % 5 === 0 ? 'strong' : 'medium',
            type: (ci + ei) % 4 === 0 ? 'supports' : 'mentions',
          });
        }
      });
    });

    return { claims, entities, relationships };
  }, [caseData]);

  if (isLoading) {
    return (
      <div className="h-full flex items-center justify-center bg-slate-950">
        <Loader2 className="w-8 h-8 text-blue-400 animate-spin" />
      </div>
    );
  }

  return (
    <div className="h-full flex flex-col bg-slate-950" dir="rtl">
      {/* Header */}
      <div className="px-6 py-4 border-b border-slate-800 bg-slate-900">
        <div className="flex items-center gap-4">
          <Link
            to={`/cases/${caseId}`}
            className="p-2 text-white/60 hover:text-white hover:bg-slate-800 rounded-lg transition-colors"
          >
            <ArrowRight className="w-5 h-5" />
          </Link>
          <div className="flex items-center gap-3">
            <div className="p-2 bg-purple-600/20 rounded-lg">
              <BarChart2 className="text-purple-400 w-5 h-5" />
            </div>
            <div>
              <h1 className="text-lg font-semibold text-white">אנליטיקה</h1>
              <p className="text-sm text-white/50">
                {caseData?.name || caseData?.case_name || 'תיק'}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-y-auto p-6">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Timeline */}
          <div className="bg-slate-900 rounded-xl border border-slate-800 overflow-hidden h-[500px]">
            <TimelineVisualization
              events={timelineEvents}
              onEventClick={(event) => {
                if (event.sourceId) {
                  navigate(`/cases/${caseId}/documents/${event.sourceId}`);
                }
              }}
            />
          </div>

          {/* Document Heatmap */}
          <div className="bg-slate-900 rounded-xl border border-slate-800 overflow-hidden h-[500px]">
            <DocumentHeatmap
              documents={heatmapData}
              onPageClick={(docId, page) => {
                navigate(`/cases/${caseId}/documents/${docId}?page=${page}`);
              }}
            />
          </div>

          {/* Distribution Charts */}
          <div className="space-y-4">
            <DistributionChart
              title="התפלגות טענות לפי סוג"
              data={claimsByType}
              type="horizontal-bar"
              className="bg-slate-900 border border-slate-800"
            />
            <DistributionChart
              title="סתירות לפי חומרה"
              data={contradictionsBySeverity}
              type="donut"
              className="bg-slate-900 border border-slate-800"
            />
          </div>

          {/* Claim Matrix */}
          <div className="bg-slate-900 rounded-xl border border-slate-800 overflow-hidden h-[400px]">
            <ClaimMatrix
              data={matrixData}
              onCellClick={(claimId, entityId) => {
                console.log('Cell clicked:', claimId, entityId);
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default CaseAnalyticsPage;
