/**
 * DistributionChart - Bar and pie charts for case analytics
 *
 * Features:
 * - Bar chart for categorical data
 * - Horizontal bars with Hebrew labels
 * - Value labels and percentages
 * - Configurable colors
 */

import React, { useMemo } from 'react';
import { BarChart2, PieChart } from 'lucide-react';

// ===========================
// Types
// ===========================

export interface ChartDataItem {
  label: string;
  value: number;
  color?: string;
}

interface DistributionChartProps {
  title: string;
  data: ChartDataItem[];
  type?: 'bar' | 'horizontal-bar' | 'donut';
  showPercentage?: boolean;
  showValues?: boolean;
  maxBars?: number;
  className?: string;
}

// ===========================
// Default Colors
// ===========================

const DEFAULT_COLORS = [
  'bg-blue-500',
  'bg-green-500',
  'bg-amber-500',
  'bg-purple-500',
  'bg-cyan-500',
  'bg-pink-500',
  'bg-red-500',
  'bg-orange-500',
];

// ===========================
// Horizontal Bar Chart
// ===========================

const HorizontalBarChart: React.FC<{
  data: ChartDataItem[];
  showPercentage: boolean;
  showValues: boolean;
  maxBars: number;
}> = ({ data, showPercentage, showValues, maxBars }) => {
  const totalValue = useMemo(
    () => data.reduce((acc, item) => acc + item.value, 0),
    [data]
  );

  const maxValue = useMemo(
    () => Math.max(...data.map((item) => item.value)),
    [data]
  );

  const displayData = data.slice(0, maxBars);
  const remainingCount = data.length - maxBars;
  const remainingValue = data
    .slice(maxBars)
    .reduce((acc, item) => acc + item.value, 0);

  return (
    <div className="space-y-3">
      {displayData.map((item, index) => {
        const percentage = totalValue > 0 ? (item.value / totalValue) * 100 : 0;
        const barWidth = maxValue > 0 ? (item.value / maxValue) * 100 : 0;
        const color = item.color || DEFAULT_COLORS[index % DEFAULT_COLORS.length];

        return (
          <div key={item.label} className="space-y-1">
            <div className="flex items-center justify-between">
              <span className="text-sm text-white/80 truncate flex-1">
                {item.label}
              </span>
              <div className="flex items-center gap-2 text-xs">
                {showValues && <span className="text-white">{item.value}</span>}
                {showPercentage && (
                  <span className="text-white/40">
                    ({percentage.toFixed(0)}%)
                  </span>
                )}
              </div>
            </div>
            <div className="h-2 bg-slate-700 rounded-full overflow-hidden">
              <div
                className={`h-full rounded-full transition-all duration-500 ${color}`}
                style={{ width: `${barWidth}%` }}
              />
            </div>
          </div>
        );
      })}

      {/* Show remaining items indicator */}
      {remainingCount > 0 && (
        <div className="text-xs text-white/40 pt-2 border-t border-slate-700">
          ועוד {remainingCount} פריטים ({remainingValue} בסה״כ)
        </div>
      )}
    </div>
  );
};

// ===========================
// Donut Chart
// ===========================

const DonutChart: React.FC<{
  data: ChartDataItem[];
  showPercentage: boolean;
  showValues: boolean;
}> = ({ data, showPercentage, showValues }) => {
  const total = useMemo(
    () => data.reduce((acc, item) => acc + item.value, 0),
    [data]
  );

  // Calculate segments
  const segments = useMemo(() => {
    let currentAngle = 0;
    return data.map((item, index) => {
      const percentage = total > 0 ? (item.value / total) * 100 : 0;
      const angle = (percentage / 100) * 360;
      const startAngle = currentAngle;
      currentAngle += angle;

      return {
        ...item,
        percentage,
        startAngle,
        endAngle: startAngle + angle,
        color: item.color || DEFAULT_COLORS[index % DEFAULT_COLORS.length],
      };
    });
  }, [data, total]);

  // Generate SVG path for donut segment
  const createArcPath = (
    startAngle: number,
    endAngle: number,
    innerRadius: number,
    outerRadius: number
  ) => {
    const startRad = ((startAngle - 90) * Math.PI) / 180;
    const endRad = ((endAngle - 90) * Math.PI) / 180;

    const x1 = 50 + outerRadius * Math.cos(startRad);
    const y1 = 50 + outerRadius * Math.sin(startRad);
    const x2 = 50 + outerRadius * Math.cos(endRad);
    const y2 = 50 + outerRadius * Math.sin(endRad);
    const x3 = 50 + innerRadius * Math.cos(endRad);
    const y3 = 50 + innerRadius * Math.sin(endRad);
    const x4 = 50 + innerRadius * Math.cos(startRad);
    const y4 = 50 + innerRadius * Math.sin(startRad);

    const largeArcFlag = endAngle - startAngle > 180 ? 1 : 0;

    return `
      M ${x1} ${y1}
      A ${outerRadius} ${outerRadius} 0 ${largeArcFlag} 1 ${x2} ${y2}
      L ${x3} ${y3}
      A ${innerRadius} ${innerRadius} 0 ${largeArcFlag} 0 ${x4} ${y4}
      Z
    `;
  };

  return (
    <div className="flex items-center gap-4">
      {/* Donut SVG */}
      <div className="relative w-32 h-32 flex-shrink-0">
        <svg viewBox="0 0 100 100" className="w-full h-full">
          {segments.map((segment, index) => (
            <path
              key={index}
              d={createArcPath(segment.startAngle, segment.endAngle, 25, 40)}
              className={segment.color.replace('bg-', 'fill-')}
              stroke="rgb(15 23 42)"
              strokeWidth="1"
            />
          ))}
        </svg>

        {/* Center label */}
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-center">
            <div className="text-lg font-bold text-white">{total}</div>
            <div className="text-[10px] text-white/40">סה״כ</div>
          </div>
        </div>
      </div>

      {/* Legend */}
      <div className="flex-1 space-y-2">
        {segments.slice(0, 6).map((segment, index) => (
          <div key={index} className="flex items-center gap-2">
            <div
              className={`w-3 h-3 rounded-sm flex-shrink-0 ${segment.color}`}
            />
            <span className="text-xs text-white/80 truncate flex-1">
              {segment.label}
            </span>
            <div className="flex items-center gap-1 text-xs">
              {showValues && <span className="text-white">{segment.value}</span>}
              {showPercentage && (
                <span className="text-white/40">
                  ({segment.percentage.toFixed(0)}%)
                </span>
              )}
            </div>
          </div>
        ))}
        {segments.length > 6 && (
          <div className="text-[10px] text-white/40">
            ועוד {segments.length - 6} קטגוריות...
          </div>
        )}
      </div>
    </div>
  );
};

// ===========================
// Main Component
// ===========================

export const DistributionChart: React.FC<DistributionChartProps> = ({
  title,
  data,
  type = 'horizontal-bar',
  showPercentage = true,
  showValues = true,
  maxBars = 8,
  className = '',
}) => {
  // Sort data by value descending
  const sortedData = useMemo(
    () => [...data].sort((a, b) => b.value - a.value),
    [data]
  );

  const total = useMemo(
    () => data.reduce((acc, item) => acc + item.value, 0),
    [data]
  );

  const Icon = type === 'donut' ? PieChart : BarChart2;

  if (data.length === 0) {
    return (
      <div className={`p-4 bg-slate-800/50 rounded-lg ${className}`}>
        <div className="flex items-center gap-2 mb-4">
          <Icon className="w-5 h-5 text-white/40" />
          <h3 className="text-sm font-medium text-white">{title}</h3>
        </div>
        <div className="flex items-center justify-center h-32">
          <p className="text-white/40 text-sm">אין נתונים לתצוגה</p>
        </div>
      </div>
    );
  }

  return (
    <div className={`p-4 bg-slate-800/50 rounded-lg ${className}`} dir="rtl">
      {/* Header */}
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-2">
          <Icon className="w-5 h-5 text-purple-400" />
          <h3 className="text-sm font-medium text-white">{title}</h3>
        </div>
        <span className="text-xs text-white/40">{total} סה״כ</span>
      </div>

      {/* Chart */}
      {type === 'donut' ? (
        <DonutChart
          data={sortedData}
          showPercentage={showPercentage}
          showValues={showValues}
        />
      ) : (
        <HorizontalBarChart
          data={sortedData}
          showPercentage={showPercentage}
          showValues={showValues}
          maxBars={maxBars}
        />
      )}
    </div>
  );
};

export default DistributionChart;
