/**
 * DocumentHeatmap - Visual heatmap showing evidence/contradiction density
 *
 * Features:
 * - Color-coded density visualization
 * - Multiple documents comparison
 * - Interactive page selection
 * - Legend with density scale
 */

import React, { useMemo, useState } from 'react';
import { FileText, AlertTriangle, Target, ChevronDown } from 'lucide-react';

// ===========================
// Types
// ===========================

export interface DocumentHeatmapData {
  documentId: string;
  documentName: string;
  totalPages: number;
  pageData: PageHeatmapData[];
}

export interface PageHeatmapData {
  page: number;
  evidenceCount: number;
  contradictionCount: number;
  claimCount: number;
  totalScore: number; // Combined importance score
}

interface DocumentHeatmapProps {
  documents: DocumentHeatmapData[];
  onPageClick?: (documentId: string, page: number) => void;
  className?: string;
}

// ===========================
// Constants
// ===========================

const DENSITY_LEVELS = [
  { min: 0, max: 0, color: 'bg-slate-700', label: 'ריק' },
  { min: 1, max: 2, color: 'bg-green-900/60', label: 'מעט' },
  { min: 3, max: 5, color: 'bg-yellow-900/60', label: 'בינוני' },
  { min: 6, max: 9, color: 'bg-orange-900/60', label: 'גבוה' },
  { min: 10, max: Infinity, color: 'bg-red-900/60', label: 'חם מאוד' },
];

function getDensityColor(score: number): string {
  const level = DENSITY_LEVELS.find((l) => score >= l.min && score <= l.max);
  return level?.color || 'bg-slate-700';
}

function getDensityLabel(score: number): string {
  const level = DENSITY_LEVELS.find((l) => score >= l.min && score <= l.max);
  return level?.label || 'לא ידוע';
}

// ===========================
// Page Cell Component
// ===========================

const PageCell: React.FC<{
  pageData: PageHeatmapData;
  documentId: string;
  onClick?: () => void;
}> = ({ pageData, documentId, onClick }) => {
  const [showTooltip, setShowTooltip] = useState(false);
  const densityColor = getDensityColor(pageData.totalScore);

  return (
    <div className="relative">
      <button
        onClick={onClick}
        onMouseEnter={() => setShowTooltip(true)}
        onMouseLeave={() => setShowTooltip(false)}
        className={`
          w-8 h-8 rounded text-[10px] font-medium
          flex items-center justify-center
          transition-all hover:scale-110 hover:z-10
          border border-slate-600/50
          ${densityColor}
          ${pageData.contradictionCount > 0 ? 'ring-1 ring-amber-500/50' : ''}
        `}
      >
        {pageData.page}
      </button>

      {/* Tooltip */}
      {showTooltip && (
        <div className="absolute bottom-full left-1/2 -translate-x-1/2 mb-2 z-20">
          <div className="bg-slate-800 border border-slate-600 rounded-lg p-2 shadow-xl min-w-[120px]">
            <div className="text-xs font-medium text-white mb-1">
              עמוד {pageData.page}
            </div>
            <div className="space-y-1">
              {pageData.evidenceCount > 0 && (
                <div className="flex items-center justify-between text-[10px]">
                  <span className="text-green-400">ראיות</span>
                  <span className="text-white">{pageData.evidenceCount}</span>
                </div>
              )}
              {pageData.contradictionCount > 0 && (
                <div className="flex items-center justify-between text-[10px]">
                  <span className="text-amber-400">סתירות</span>
                  <span className="text-white">{pageData.contradictionCount}</span>
                </div>
              )}
              {pageData.claimCount > 0 && (
                <div className="flex items-center justify-between text-[10px]">
                  <span className="text-blue-400">טענות</span>
                  <span className="text-white">{pageData.claimCount}</span>
                </div>
              )}
            </div>
            <div className="mt-1 pt-1 border-t border-slate-600">
              <div className="text-[9px] text-white/50">
                {getDensityLabel(pageData.totalScore)}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

// ===========================
// Document Row Component
// ===========================

const DocumentRow: React.FC<{
  document: DocumentHeatmapData;
  onPageClick?: (page: number) => void;
  isExpanded: boolean;
  onToggle: () => void;
}> = ({ document, onPageClick, isExpanded, onToggle }) => {
  // Create page data map for quick lookup
  const pageDataMap = useMemo(() => {
    const map = new Map<number, PageHeatmapData>();
    document.pageData.forEach((pd) => map.set(pd.page, pd));
    return map;
  }, [document.pageData]);

  // Calculate stats
  const stats = useMemo(() => {
    const total = document.pageData.reduce((acc, pd) => acc + pd.totalScore, 0);
    const contradictions = document.pageData.reduce(
      (acc, pd) => acc + pd.contradictionCount,
      0
    );
    const evidence = document.pageData.reduce(
      (acc, pd) => acc + pd.evidenceCount,
      0
    );
    return { total, contradictions, evidence };
  }, [document.pageData]);

  // Generate page array
  const pages = useMemo(
    () => Array.from({ length: document.totalPages }, (_, i) => i + 1),
    [document.totalPages]
  );

  return (
    <div className="border border-slate-700 rounded-lg overflow-hidden">
      {/* Header */}
      <button
        onClick={onToggle}
        className="w-full flex items-center justify-between px-4 py-3 bg-slate-800/50 hover:bg-slate-800 transition-colors"
      >
        <div className="flex items-center gap-3">
          <FileText className="w-4 h-4 text-white/60" />
          <span className="text-sm font-medium text-white truncate max-w-[200px]">
            {document.documentName}
          </span>
          <span className="text-xs text-white/40">{document.totalPages} עמודים</span>
        </div>

        <div className="flex items-center gap-4">
          {/* Stats badges */}
          <div className="flex items-center gap-2">
            {stats.contradictions > 0 && (
              <span className="flex items-center gap-1 text-xs text-amber-400 bg-amber-500/10 px-2 py-0.5 rounded">
                <AlertTriangle className="w-3 h-3" />
                {stats.contradictions}
              </span>
            )}
            {stats.evidence > 0 && (
              <span className="flex items-center gap-1 text-xs text-green-400 bg-green-500/10 px-2 py-0.5 rounded">
                <Target className="w-3 h-3" />
                {stats.evidence}
              </span>
            )}
          </div>

          <ChevronDown
            className={`w-4 h-4 text-white/40 transition-transform ${
              isExpanded ? 'rotate-180' : ''
            }`}
          />
        </div>
      </button>

      {/* Expanded content */}
      {isExpanded && (
        <div className="p-4 bg-slate-900/50">
          <div className="flex flex-wrap gap-1">
            {pages.map((page) => {
              const pd = pageDataMap.get(page) || {
                page,
                evidenceCount: 0,
                contradictionCount: 0,
                claimCount: 0,
                totalScore: 0,
              };
              return (
                <PageCell
                  key={page}
                  pageData={pd}
                  documentId={document.documentId}
                  onClick={() => onPageClick?.(page)}
                />
              );
            })}
          </div>
        </div>
      )}
    </div>
  );
};

// ===========================
// Legend Component
// ===========================

const Legend: React.FC = () => {
  return (
    <div className="flex items-center gap-3 flex-wrap">
      <span className="text-xs text-white/50">צפיפות:</span>
      {DENSITY_LEVELS.map((level, i) => (
        <div key={i} className="flex items-center gap-1">
          <div className={`w-4 h-4 rounded ${level.color} border border-slate-600/50`} />
          <span className="text-[10px] text-white/40">{level.label}</span>
        </div>
      ))}
    </div>
  );
};

// ===========================
// Main Component
// ===========================

export const DocumentHeatmap: React.FC<DocumentHeatmapProps> = ({
  documents,
  onPageClick,
  className = '',
}) => {
  const [expandedDocs, setExpandedDocs] = useState<Set<string>>(
    new Set(documents.slice(0, 1).map((d) => d.documentId))
  );

  const toggleDocument = (docId: string) => {
    setExpandedDocs((prev) => {
      const next = new Set(prev);
      if (next.has(docId)) {
        next.delete(docId);
      } else {
        next.add(docId);
      }
      return next;
    });
  };

  if (documents.length === 0) {
    return (
      <div className={`flex items-center justify-center h-64 ${className}`}>
        <div className="text-center">
          <FileText className="w-12 h-12 text-white/20 mx-auto mb-3" />
          <p className="text-white/40 text-sm">אין מסמכים לתצוגה</p>
        </div>
      </div>
    );
  }

  return (
    <div className={`flex flex-col h-full ${className}`} dir="rtl">
      {/* Header */}
      <div className="px-4 py-3 border-b border-slate-700 bg-slate-800/50">
        <div className="flex items-center justify-between mb-3">
          <h2 className="text-lg font-semibold text-white flex items-center gap-2">
            <Target className="w-5 h-5 text-purple-400" />
            מפת חום מסמכים
          </h2>
          <span className="text-xs text-white/40">{documents.length} מסמכים</span>
        </div>

        <Legend />
      </div>

      {/* Documents list */}
      <div className="flex-1 overflow-y-auto p-4 space-y-3">
        {documents.map((doc) => (
          <DocumentRow
            key={doc.documentId}
            document={doc}
            onPageClick={(page) => onPageClick?.(doc.documentId, page)}
            isExpanded={expandedDocs.has(doc.documentId)}
            onToggle={() => toggleDocument(doc.documentId)}
          />
        ))}
      </div>
    </div>
  );
};

export default DocumentHeatmap;
