/**
 * ClaimMatrix - Matrix showing relationships between claims and entities
 *
 * Features:
 * - Cross-reference claims with entities/sources
 * - Color-coded relationship strength
 * - Interactive cell selection
 * - Summary statistics
 */

import React, { useMemo, useState } from 'react';
import { Grid3X3, ChevronLeft, ChevronRight } from 'lucide-react';

// ===========================
// Types
// ===========================

export interface ClaimMatrixData {
  claims: { id: string; label: string }[];
  entities: { id: string; label: string }[];
  relationships: MatrixRelationship[];
}

export interface MatrixRelationship {
  claimId: string;
  entityId: string;
  strength: 'strong' | 'medium' | 'weak' | 'none';
  type?: 'supports' | 'contradicts' | 'mentions';
  details?: string;
}

interface ClaimMatrixProps {
  data: ClaimMatrixData;
  onCellClick?: (claimId: string, entityId: string) => void;
  className?: string;
}

// ===========================
// Constants
// ===========================

const STRENGTH_CONFIG: Record<
  string,
  { color: string; bgColor: string; label: string }
> = {
  strong: {
    color: 'text-green-400',
    bgColor: 'bg-green-500/30',
    label: 'חזק',
  },
  medium: {
    color: 'text-yellow-400',
    bgColor: 'bg-yellow-500/30',
    label: 'בינוני',
  },
  weak: {
    color: 'text-orange-400',
    bgColor: 'bg-orange-500/20',
    label: 'חלש',
  },
  none: {
    color: 'text-white/20',
    bgColor: 'bg-slate-700/30',
    label: 'אין',
  },
};

const TYPE_SYMBOLS: Record<string, { symbol: string; color: string }> = {
  supports: { symbol: '✓', color: 'text-green-400' },
  contradicts: { symbol: '✗', color: 'text-red-400' },
  mentions: { symbol: '•', color: 'text-blue-400' },
};

// ===========================
// Matrix Cell
// ===========================

const MatrixCell: React.FC<{
  relationship?: MatrixRelationship;
  onClick?: () => void;
}> = ({ relationship, onClick }) => {
  const [showTooltip, setShowTooltip] = useState(false);
  const strength = relationship?.strength || 'none';
  const config = STRENGTH_CONFIG[strength];
  const typeConfig = relationship?.type ? TYPE_SYMBOLS[relationship.type] : null;

  return (
    <div className="relative">
      <button
        onClick={onClick}
        onMouseEnter={() => setShowTooltip(true)}
        onMouseLeave={() => setShowTooltip(false)}
        className={`
          w-10 h-10 flex items-center justify-center
          border border-slate-700/50 transition-all
          hover:scale-110 hover:z-10 hover:shadow-lg
          ${config.bgColor}
        `}
      >
        {typeConfig ? (
          <span className={`text-sm font-bold ${typeConfig.color}`}>
            {typeConfig.symbol}
          </span>
        ) : strength !== 'none' ? (
          <span className={`text-lg ${config.color}`}>●</span>
        ) : (
          <span className="text-white/10">-</span>
        )}
      </button>

      {/* Tooltip */}
      {showTooltip && relationship && relationship.strength !== 'none' && (
        <div className="absolute bottom-full left-1/2 -translate-x-1/2 mb-2 z-20">
          <div className="bg-slate-800 border border-slate-600 rounded-lg p-2 shadow-xl min-w-[100px] text-center">
            <div className={`text-xs font-medium ${config.color}`}>
              {config.label}
            </div>
            {relationship.type && (
              <div className="text-[10px] text-white/50 mt-1">
                {relationship.type === 'supports' && 'תומך'}
                {relationship.type === 'contradicts' && 'סותר'}
                {relationship.type === 'mentions' && 'מוזכר'}
              </div>
            )}
            {relationship.details && (
              <div className="text-[10px] text-white/40 mt-1 max-w-[150px] truncate">
                {relationship.details}
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

// ===========================
// Legend
// ===========================

const Legend: React.FC = () => {
  return (
    <div className="flex flex-wrap items-center gap-4">
      <span className="text-xs text-white/50">עוצמת קשר:</span>
      {Object.entries(STRENGTH_CONFIG)
        .filter(([key]) => key !== 'none')
        .map(([key, config]) => (
          <div key={key} className="flex items-center gap-1">
            <div className={`w-4 h-4 rounded ${config.bgColor}`} />
            <span className="text-[10px] text-white/40">{config.label}</span>
          </div>
        ))}

      <div className="w-px h-4 bg-slate-600" />

      <span className="text-xs text-white/50">סוג:</span>
      {Object.entries(TYPE_SYMBOLS).map(([key, config]) => (
        <div key={key} className="flex items-center gap-1">
          <span className={`text-sm font-bold ${config.color}`}>
            {config.symbol}
          </span>
          <span className="text-[10px] text-white/40">
            {key === 'supports' && 'תומך'}
            {key === 'contradicts' && 'סותר'}
            {key === 'mentions' && 'מוזכר'}
          </span>
        </div>
      ))}
    </div>
  );
};

// ===========================
// Main Component
// ===========================

export const ClaimMatrix: React.FC<ClaimMatrixProps> = ({
  data,
  onCellClick,
  className = '',
}) => {
  const [claimOffset, setClaimOffset] = useState(0);
  const [entityOffset, setEntityOffset] = useState(0);

  const MAX_VISIBLE_CLAIMS = 8;
  const MAX_VISIBLE_ENTITIES = 6;

  // Build relationship lookup map
  const relationshipMap = useMemo(() => {
    const map = new Map<string, MatrixRelationship>();
    data.relationships.forEach((rel) => {
      map.set(`${rel.claimId}-${rel.entityId}`, rel);
    });
    return map;
  }, [data.relationships]);

  // Get visible claims and entities
  const visibleClaims = useMemo(
    () => data.claims.slice(claimOffset, claimOffset + MAX_VISIBLE_CLAIMS),
    [data.claims, claimOffset]
  );

  const visibleEntities = useMemo(
    () => data.entities.slice(entityOffset, entityOffset + MAX_VISIBLE_ENTITIES),
    [data.entities, entityOffset]
  );

  // Calculate summary stats
  const stats = useMemo(() => {
    const byType = { supports: 0, contradicts: 0, mentions: 0 };
    const byStrength = { strong: 0, medium: 0, weak: 0 };

    data.relationships.forEach((rel) => {
      if (rel.type && byType[rel.type] !== undefined) {
        byType[rel.type]++;
      }
      if (rel.strength !== 'none' && byStrength[rel.strength] !== undefined) {
        byStrength[rel.strength]++;
      }
    });

    return { byType, byStrength, total: data.relationships.length };
  }, [data.relationships]);

  if (data.claims.length === 0 || data.entities.length === 0) {
    return (
      <div className={`flex items-center justify-center h-64 ${className}`}>
        <div className="text-center">
          <Grid3X3 className="w-12 h-12 text-white/20 mx-auto mb-3" />
          <p className="text-white/40 text-sm">אין נתונים לתצוגה</p>
        </div>
      </div>
    );
  }

  return (
    <div className={`flex flex-col h-full ${className}`} dir="rtl">
      {/* Header */}
      <div className="px-4 py-3 border-b border-slate-700 bg-slate-800/50">
        <div className="flex items-center justify-between mb-3">
          <h2 className="text-lg font-semibold text-white flex items-center gap-2">
            <Grid3X3 className="w-5 h-5 text-cyan-400" />
            מטריצת טענות-ישויות
          </h2>
          <div className="flex items-center gap-3 text-xs text-white/40">
            <span>{data.claims.length} טענות</span>
            <span>•</span>
            <span>{data.entities.length} ישויות</span>
            <span>•</span>
            <span>{stats.total} קשרים</span>
          </div>
        </div>

        <Legend />
      </div>

      {/* Matrix */}
      <div className="flex-1 overflow-auto p-4">
        <div className="inline-block min-w-full">
          {/* Header row with entity labels */}
          <div className="flex">
            {/* Empty corner cell */}
            <div className="w-32 h-10 flex-shrink-0" />

            {/* Entity navigation */}
            {entityOffset > 0 && (
              <button
                onClick={() =>
                  setEntityOffset(Math.max(0, entityOffset - MAX_VISIBLE_ENTITIES))
                }
                className="w-10 h-10 flex items-center justify-center text-white/40 hover:text-white hover:bg-slate-700/50 rounded"
              >
                <ChevronRight className="w-4 h-4" />
              </button>
            )}

            {/* Entity labels */}
            {visibleEntities.map((entity) => (
              <div
                key={entity.id}
                className="w-10 h-10 flex items-center justify-center"
              >
                <span
                  className="text-[9px] text-white/60 font-medium truncate rotate-[-45deg] whitespace-nowrap max-w-[60px]"
                  title={entity.label}
                >
                  {entity.label}
                </span>
              </div>
            ))}

            {/* Entity navigation */}
            {entityOffset + MAX_VISIBLE_ENTITIES < data.entities.length && (
              <button
                onClick={() => setEntityOffset(entityOffset + MAX_VISIBLE_ENTITIES)}
                className="w-10 h-10 flex items-center justify-center text-white/40 hover:text-white hover:bg-slate-700/50 rounded"
              >
                <ChevronLeft className="w-4 h-4" />
              </button>
            )}
          </div>

          {/* Claims navigation */}
          {claimOffset > 0 && (
            <div className="flex items-center">
              <button
                onClick={() =>
                  setClaimOffset(Math.max(0, claimOffset - MAX_VISIBLE_CLAIMS))
                }
                className="w-32 h-6 flex items-center justify-center text-white/40 hover:text-white text-xs"
              >
                ↑ טענות קודמות
              </button>
            </div>
          )}

          {/* Data rows */}
          {visibleClaims.map((claim) => (
            <div key={claim.id} className="flex items-center">
              {/* Claim label */}
              <div className="w-32 h-10 flex items-center pr-2 flex-shrink-0">
                <span
                  className="text-xs text-white/80 truncate"
                  title={claim.label}
                >
                  {claim.label}
                </span>
              </div>

              {/* Spacer for left nav */}
              {entityOffset > 0 && <div className="w-10 h-10" />}

              {/* Matrix cells */}
              {visibleEntities.map((entity) => (
                <MatrixCell
                  key={`${claim.id}-${entity.id}`}
                  relationship={relationshipMap.get(`${claim.id}-${entity.id}`)}
                  onClick={() => onCellClick?.(claim.id, entity.id)}
                />
              ))}
            </div>
          ))}

          {/* Claims navigation */}
          {claimOffset + MAX_VISIBLE_CLAIMS < data.claims.length && (
            <div className="flex items-center">
              <button
                onClick={() => setClaimOffset(claimOffset + MAX_VISIBLE_CLAIMS)}
                className="w-32 h-6 flex items-center justify-center text-white/40 hover:text-white text-xs"
              >
                ↓ טענות נוספות ({data.claims.length - claimOffset - MAX_VISIBLE_CLAIMS})
              </button>
            </div>
          )}
        </div>
      </div>

      {/* Stats footer */}
      <div className="px-4 py-2 border-t border-slate-700 bg-slate-800/30">
        <div className="flex items-center justify-between text-xs">
          <div className="flex items-center gap-4">
            <span className="text-white/50">סיכום:</span>
            <span className="text-green-400">{stats.byType.supports} תומכים</span>
            <span className="text-red-400">{stats.byType.contradicts} סותרים</span>
            <span className="text-blue-400">{stats.byType.mentions} מוזכרים</span>
          </div>
          <div className="flex items-center gap-3">
            <span className="text-white/40">
              חזק: {stats.byStrength.strong}
            </span>
            <span className="text-white/40">
              בינוני: {stats.byStrength.medium}
            </span>
            <span className="text-white/40">
              חלש: {stats.byStrength.weak}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ClaimMatrix;
